import React, { useState, useEffect } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { auth } from '../firebase';
import { getUser } from '../services/userService';
import { logoutUser } from '../services/authService';
import ChangePassword from './ChangePassword';

function DashboardAdmin() {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [showChangePassword, setShowChangePassword] = useState(false);
  const history = useHistory();

  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged(async (authUser) => {
      if (authUser) {
        try {
          const userData = await getUser(authUser.uid);
          if (userData && userData.role === 'admin') {
            setUser(userData);
          } else {
            setError('Utilisateur non autoris ou donnes manquantes');
            history.push('/');
          }
        } catch (error) {
          console.error('Erreur lors de la rcupration des donnes:', error);
          setError('Erreur lors de la rcupration des donnes: ' + error.message);
        }
      } else {
        setUser(null);
        history.push('/');
      }
      setLoading(false);
    });

    return () => unsubscribe();
  }, [history]);

  const handleLogout = async () => {
    try {
      await logoutUser();
      history.push('/');
    } catch (error) {
      console.error("Erreur lors de la dconnexion:", error);
      setError('Erreur lors de la dconnexion: ' + error.message);
    }
  };

  if (loading) {
    return <div>Chargement...</div>;
  }

  if (error) {
    return <div>Erreur: {error}</div>;
  }

  if (!user) {
    return <div>Utilisateur non trouv</div>;
  }

  return (
    <div className="dashboard-container">
      <h1>Tableau de bord Administrateur</h1>
      <nav>
        <ul>
          <li><Link to="/gestion-utilisateurs">Gestion des utilisateurs</Link></li>
          <li><Link to="/gestion-planning-admin">Gestion du planning</Link></li>
          <li><Link to="/gestion-periode-saisie">Gestion de la priode de saisie</Link></li>
          <li><button onClick={() => setShowChangePassword(!showChangePassword)}>
            {showChangePassword ? 'Masquer' : 'Modifier le mot de passe'}
          </button></li>
        </ul>
      </nav>
      <div className="dashboard-content">
        <h2>Bienvenue, {user.prenom} {user.nom}</h2>
        <p>Ici, vous pouvez accder  vos fonctionnalits principales :</p>
        <ul>
          <li>Grer les utilisateurs</li>
          <li>Grer le planning</li>
          <li>Dfinir la priode de saisie des desiderata</li>
          <li>Modifier votre mot de passe</li>
        </ul>
        {showChangePassword && <ChangePassword />}
      </div>
      <button onClick={handleLogout}>Se dconnecter</button>
    </div>
  );
}

export default DashboardAdmin;